*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/* VARIABLES DECLARATIONS */

:root {
  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --light-blue: #0083c0;
  --lighter-blue: #2ba4dc;
  --dark-blue: #4f46e5;
  --red: #EA5B6F;
  --orange: #FF894F;
  --yellow: #FFCB61;
}

/* PAGE STYLING */

:root {
  font-family: var(--system-ui);
  background-color: var(--light-blue);
  color: white;
}

body {
  min-height: 100lvh;
}

/* Header */

header {
  padding: 1.5rem 0;
  height: 2svh;
}

.header-container {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.5rem;
}

.header-container h1 {
  font-size: 1.8rem;
  font-weight: 700;
}

.header-container nav a {
  color: white;
  text-decoration: none;
  margin-left: 1.5rem;
}

.header-container nav a:hover {
  opacity: 0.8;
}

/* Main container */

main {
  background: linear-gradient(to bottom, var(--light-blue) 0%, black 30%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main-container {
  position: relative;
  min-height: 98svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center; 
}

.background-media {
  position: absolute;
  height: 90%;
  width: 100%;
  mask-image: linear-gradient(0.25turn, rgba(255, 255, 255, 0), var(--yellow) 20%, var(--yellow) 80%, rgba(255, 255, 255, 0));
}

.background-media video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  mask-image: linear-gradient(rgba(255, 255, 255, 0), var(--yellow) 20%, var(--yellow) 80%, rgba(255, 255, 255, 0));
}

.get-started-container {
  background-color: var(--yellow);
  z-index: 1;
  border-radius: 30px;
  padding: 60px 20px;
  margin: 10px;
}

.get-started-container h1 {
  font-size: 2rem;
  font-weight: 1000;
  margin-bottom: 1rem;
  color: black;
}

.get-started-container h1 em {
  color: var(--lighter-blue);
  text-transform: none;
}

.get-started-container p {
  max-width: 600px;
  margin: 0 auto 2rem;
  font-size: 1.1rem;
  line-height: 1.6;
  color: black;
}

.get-started-container button {
  color: white;
  font-weight: bold;
  background-color: var(--red);
  border: 0;
  border-radius: 100px;
  padding: 10px 30px;
  transition: background 0.3s ease;
}

.get-started-container button:hover {
  background-color: var(--light-blue);
  cursor: pointer;
  transition: background 0.3s ease;
}

/* Footer */

.footer {
  background-color: black;
  text-align: center;
  padding: 1.5rem 0;
  font-size: 0.9rem;
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA,cAAc;AACd;EACE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA,2BAA2B;;AAE3B;EACE,kIAAkI;EAClI,qBAAqB;EACrB,uBAAuB;EACvB,oBAAoB;EACpB,cAAc;EACd,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA,iBAAiB;;AAEjB;EACE,6BAA6B;EAC7B,mCAAmC;EACnC,YAAY;AACd;;AAEA;EACE,kBAAkB;AACpB;;AAEA,WAAW;;AAEX;EACE,iBAAiB;EACjB,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,cAAc;EACd,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,qBAAqB;EACrB,mBAAmB;AACrB;;AAEA;EACE,YAAY;AACd;;AAEA,mBAAmB;;AAEnB;EACE,uEAAuE;EACvE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,iBAAiB;EACjB,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,WAAW;EACX,2HAA2H;AAC7H;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,iBAAiB;EACjB,iHAAiH;AACnH;;AAEA;EACE,+BAA+B;EAC/B,UAAU;EACV,mBAAmB;EACnB,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,0BAA0B;EAC1B,oBAAoB;AACtB;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,iBAAiB;EACjB,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,iBAAiB;EACjB,4BAA4B;EAC5B,SAAS;EACT,oBAAoB;EACpB,kBAAkB;EAClB,gCAAgC;AAClC;;AAEA;EACE,mCAAmC;EACnC,eAAe;EACf,gCAAgC;AAClC;;AAEA,WAAW;;AAEX;EACE,uBAAuB;EACvB,kBAAkB;EAClB,iBAAiB;EACjB,iBAAiB;AACnB","sourcesContent":["/* CSS RESET */\n*, *::before, *::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\n\ninput, button, textarea, select {\n  font: inherit;\n}\n\np, h1, h2, h3, h4, h5, h